import { Component, h, Method, Element, State } from '@stencil/core';
import Swiper, {Pagination, Thumbs} from 'swiper';

Swiper.use([Pagination, Thumbs]);

@Component({
	tag: 'ks-error-popup',
	styleUrl: 'error-popup.css'
})
export class ErrorPopup {
    @Element() root: HTMLElement;
    overlay: HTMLKsOverlayElement;

    @State() name: string = "";
    @State() message: string = "";
    @State() stack: string = "";

    componentDidRender() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    @State() visible: string = "";

    @Method() async show(error: Error) {
        this.name = error.name;
        this.message = error.message;
        this.stack = error.stack === undefined ? "" : error.stack;

        this.overlay.show();
        this.visible = "visible";
    }

    @Method() async hide() {
        this.visible = "hidden";
        this.overlay.hide();
    }

	render() {
        let name = `${this.name}: ${this.message}`
        let message = "";
        let stack = this.stack;

        const status = parseInt(this.name)

        if(!isNaN(status)) {
            if(status >= 400 && status < 500)
                message = `WystÄ…piÅ‚ bÅ‚Ä…d aplikacji. `;

            if(status >= 500)
                message = `WystÄ…piÅ‚ bÅ‚Ä…d serwera. `;

            stack += name;
        }
        else message = "WystÄ…piÅ‚ bÅ‚Ä…d aplikacji. ";

        message += "JeÅ¼eli problem siÄ™ powtarza prosimy o kontakt mailowy lub telefoniczny oraz podanie informacji w poniÅ¼szej ramce.";

        const userAgent = `User-agent: ${navigator.userAgent}`;

		return <ks-overlay dark>
            <div class={`content ${this.visible}`}>
                <div class="bar">
                    <div class="title">{name}</div>

                    <div class="close" onClick={()=>this.hide()}>
                        <ks-icon name="x" size={1.2}></ks-icon>
                    </div>
                </div>

                <div class="message">
                    {message}
                </div>

                <div class="data">
                    {userAgent}
                    <span class="stack">{stack}</span>
                </div>
            </div>
        </ks-overlay>;
	}
}