import { Component, Build, h } from '@stencil/core';
import { common } from "../../../global/data/common";

@Component({
	tag: 'ks-page-footer',
	styleUrl: 'footer.css'
})
export class PageFooter {
	render() {
		const phone = common.get('phone');
		const email = common.get('email');
		const time = common.get('workingHours');
		const company = common.get('company');
		const address = common.get('address');
		const softwareLink = common.get('softwareLink');

		const strings = common.get('translations');

		return[
			<div class="about">
				<div class="links">
					{common.get('footerLinks').map(section =>
						<ks-footer-links heading={section.name}>
							{section.items.map(item =>
								<li><a href={item.link}>{item.name}</a></li>
							)}
						</ks-footer-links>
					)}

					<div class="contact">
						<span>{strings.footerContact}</span>
						
						<a style={{"display": "none"}}></a>

						<a href={`tel:${phone}`}>
							<ks-icon name="phone"/>
							<span>{phone}</span>
						</a>

						<a href={`mailto:${email}`}>
							<ks-icon name="mail"/>
							<span>{Build.isBrowser ? email : ""}</span> 
						</a>

						<span><ks-icon name="clock" size={0.9}/> {time}</span>
						<span><ks-icon name="home" size={0.9}/> {company}</span>
						<span><ks-icon name="map-pin" size={0.9}/> {address}</span>
					</div>
				</div>

				<div class="newsletter">
					<div>{strings.footerNewsletterSmall}</div>
					<div>{strings.footerNewsletterLarge}</div>
					<ks-button light border name={strings.footerNewsletterSubmit} onClick={()=> document.querySelector('ks-newsletter-popup-edrone')?.Show()}/>
				</div>
			</div>,
		
			<div class="portals">
				<div>
					{common.get('social').map(social =>
						<ks-footer-button width={social.width} height={social.height} href={social.link} image={social.image} />
					)}
				</div>

				<div>
					{common.get('reviewers').map(reviewer =>
						<ks-footer-button width={reviewer.width} height={reviewer.height} href={reviewer.link} image={reviewer.image} />
					)}
				</div>
			</div>,
			<div class="software">
				<a href={softwareLink} rel="nofollow">{strings.footerSoftware}</a>
			</div>
		];
	}
}