import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-article-card',
	styleUrl: 'articleCard.css'
})
export class ArticleCard {
	@Prop() image: string;
	@Prop() webp: string;
	@Prop() heading: string;
	@Prop() date: string;
	@Prop() views: number;
	@Prop() link: string;

	@Prop() width: number;
	@Prop() height: number;

	@Prop({reflect: true}) hideOnOdd: boolean = false;
	@Prop({reflect: true}) flat: boolean = false;

	render() {
		return [
			<a href={this.link} class="image">
				<ks-img2 horizontal center src={this.image} webp={this.webp} alt={this.heading} width={600} height={300} />
			</a>,

			<div class="info">
				<h3><a href={this.link}>{this.heading}</a></h3>
				<div class="meta">
					<ks-icon name="calendar" size={0.9}/> {this.date}
				</div>
				
				<slot/>
			</div>,
			<div class="footer">
				<a href={this.link}>Zobacz wiÄ™cej</a>
			</div>
		];
	}
}
