ks-article-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background-color: white;
    box-shadow: var(--card-shadow);
}

ks-article-card[flat] {
    border: #ededed 1px solid;
    border-radius: 6px;
    overflow: hidden;
    box-shadow: none;
}

ks-article-card[flat] .footer {
    border-color: #ededed;
}

ks-article-card .image {
    position: relative;
    padding-top: 50%;
    max-width: 100%;
    overflow: hidden;
}

ks-article-card .image > * {
    position: absolute;
    top: 0;
}


ks-article-card .info {
    padding: 15px;
    flex: 1 0 auto;

    font-size: .875rem;
    line-height: 1.5;
}


ks-article-card .info h3 {
    margin-bottom: 5px;
}

ks-article-card .info a {
    text-decoration: none !important;
    color: #151515;
    transition: var(--transition-color);
}

ks-article-card .info a:hover {
    color: var(--color-secondary);
}

ks-article-card .info a:active {
    color: var(--color-secondary-hover);
}


ks-article-card .meta {
    font-size: .875rem;
    line-height: 1.4;
    color: #707070;
}

ks-article-card .meta ks-icon:not(:first-of-type) {
    margin-left: 15px;
}


ks-article-card .footer {
    padding: 15px;
    text-align: center;
    position: relative;

    border-top: 1px solid #e2e2e2;
}

ks-article-card .footer a {
    text-decoration: none !important;
    user-select: none;
    color: #151515;
    font-size: .875rem;
    line-height: 1.4;
    position: relative;

    opacity: 1;
    transition: opacity 0.2s ease;
}

ks-article-card .footer a:active {
    opacity: 0.6;
}

ks-article-card .footer a:before {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -2px;
    transform: translateX(-50%);

    height: 1px;
    width: 0%;
    border-bottom: 1px solid #151515;

    opacity: 1;
    transition: width 0.3s ease, opacity 0.2s ease;
}

ks-article-card .footer a:hover:before {
    width: 100%;
}

ks-article-card .footer a:active:before {
    opacity: 0.6;
    width: 100%;
}


@media screen and (min-width: 960px) and (max-width: 1200px) {
    ks-article-card[hide-on-odd] {
        display: none !important;
    }
}

