import { Component, State, h } from '@stencil/core';
import { common } from "../../../../global/data/common";
@Component({
    tag: 'ks-navbar-categories',
    styleUrl: 'navbar-categories.css'
})
export class NavbarCategories {

    @State() active: number = -1;
    @State() childActive: number = 0;
    @State() hidden = true;
    @State() hiddenO = true;

    timeout: NodeJS.Timeout;
    delaytimeout: NodeJS.Timeout;

    mouseOver(index: number, deep: boolean) {
        clearTimeout(this.timeout);
        clearTimeout(this.delaytimeout);

        this.delaytimeout = setTimeout(() => {
            
            this.hidden = false;
            this.hiddenO = false;

            this.active = index;
            this.NavbarColor(!deep);
        }, 200);
    }

    mouseLeave() {
        clearTimeout(this.delaytimeout);
        this.delaytimeout = setTimeout(() => {
            this.hiddenO = true;
            this.timeout = setTimeout(() => {
                this.hidden = true;
            }, 200);
            
            this.NavbarColor(true);
        }, 200);
    }

    NavbarColor(state: boolean) {
        const bar = document.querySelector("ks-navbar-categories") as HTMLElement;
        if(!bar) return;

        bar.style.backgroundColor = state ? "var(--navbar-category-color)" : "var(--navbar-color)";
        bar.style.borderTop = state ? "1px solid transparent" : "1px solid var(--navbar-category-color)";
        bar.style.borderBottom = bar.style.borderTop;
    }

    render() {
        return common.get('categories').map( (category, categoryIndex) => {
            const count = category.children.length;
            const childrenstyle = {
                opacity: this.hiddenO ? "0.0" : "1.0",
                height: (count * 40) + "px"
            };

            const deep = category.deep;
            const classList = ["category", !deep ? "shallow" : null].join(' ');
    
            return <div class={classList} onMouseOver={()=>this.mouseOver(categoryIndex, deep)} onMouseLeave={()=>this.mouseLeave()}>
                <a href={category.url}>{category.name}</a>

                {count > 0 ?
                    <ks-icon name="chevron-down" size={0.8} />
                : null}

                {deep ? [
                    <div class="expandedchildren" style={childrenstyle} hidden={this.hidden || this.active != categoryIndex}>
    
                        <div class="buttons">
                            {category.children.map( (child, index) => 
                                <a href={child.url}
                                class={'children' in child && index == this.childActive ? "active" : null}
                                onMouseOver={()=> 'children' in child ? this.childActive = index : null}
                                >
                                    {child.name}
        
                                    {'children' in child ? null :
                                        <ks-icon name="link" size={0.65}/>
                                    }
                                </a>
                            )}
                        </div>
                                    
                        <div class="content">
                            {category.children.map( (child, index) => 'children' in child ?
        
                                <div hidden={index != this.childActive}>
                                    {child.children.map( sub => 
                                        <a href={sub.url}>{sub.name}</a>
                                    )}
                                </div>
                                
                            : null )}
                        </div>
        
                        <div class="graphic">
                            {category.children.map((category, index) => 
                                "image" in category ?
                                    <ks-img2 vertical src={category.image.url}
                                            width={category.image.width}
                                            height={category.image.height}
                                            target="ks-navbar-categories .expandedchildren > .graphic"
                                            style={{display: (this.childActive == index) ? "block": "none"}}>
                                    </ks-img2>
                                :null
                            )}
                        </div>
                    </div>

                ]: 
                
                    <div class="simplechildren" style={childrenstyle} hidden={this.hidden || this.active != categoryIndex}>
                        {category.children.map(child => 
                            <a href={child.url}>{child.name}</a>    
                        )}
                    </div>
                }
            </div>
        });
    }
}