import { Component, h, Method, Element, Prop, Event, EventEmitter } from '@stencil/core';

@Component({
	tag: 'ks-overlay',
	styleUrl: 'overlay.css'
})
export class Overlay {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) dark: boolean = false;
    @Prop({mutable: true}) close: boolean = true;

    @Event() closed: EventEmitter;

    timeout: NodeJS.Timeout;
    timeoutLength: number = 300;

    componentDidLoad() {
        this.root.addEventListener('mousedown', (e) => {
            if(e.currentTarget == e.target && this.close) this.hide()
        }, false);
    }

    @Method() async show() {
        clearTimeout(this.timeout);
        this.scrollToggle()
        this.root.classList.add("active");
        this.root.style.display = "flex";
    }

    @Method() async hide() {
        clearTimeout(this.timeout);
        this.scrollToggle();
        this.root.classList.remove("active");
        this.timeout = setTimeout(() => {
            this.root.style.display = "none";
        }, this.timeoutLength);

        this.closed.emit();
    }

    body = document.body as HTMLBodyElement;
    scroll: number;

    scrollToggle() {
        if(this.body.style.position == "fixed") {
            this.body.style.position = "static";
            this.body.style.overflowY = "auto";
            this.body.style.width = "unset";
            
            window.scrollTo(0, this.scroll);
            return;
        }

        this.scroll = window.scrollY;
        this.body.style.position = "fixed";
        this.body.style.top = `${-this.scroll}px`;
        this.body.style.overflowY = "scroll";
        this.body.style.width = "100%";
    }

	render() {
		return <slot></slot>;
	}
}
