import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-navbar-button',
    styleUrl: 'navbar-button.css'
})
export class NavbarButton {

    @Prop() link: string;
    @Prop() icon: string;
    @Prop() count: number;
    @Prop() name: string;

    render() {

        return [
            <a href={this.link}
               aria-label={this.name}>
                <ks-icon name={this.icon} size={1.4}></ks-icon>
                {this.count && this.count != 0 ? 
                    <span class="count">{this.count}</span>
                : null}
            </a>,
            <div>
                {this.name}
            </div>
        ];
    }
}