ks-navbar-contact-panel {
    display: none;
    flex-direction: column;
    align-items: flex-end;

    position: absolute;
    top: 0px;
    left: 0px;
    
    overflow: hidden;

    width: 100%;

    color: var(--navbar-category-text-color);;

    z-index: 100000;
}

ks-navbar-contact-panel.visible {
    animation: fade-in 0.3s ease 1;
}

ks-navbar-contact-panel.hidden {
    animation: fade-out 0.3s ease 1;
}

ks-navbar-contact-panel[toggled] {
    display: flex;
}

ks-navbar-contact-panel .navbar {
    display: flex;
    flex-direction: row-reverse;

    position: relative;
    right: 0px;

    height: 70px;
    background-color: var(--navbar-color);
}

ks-navbar-contact-panel .content {
    display: flex;
    width: 100%;

    background-color: var(--navbar-category-color);
    box-shadow: 0 6px 6px rgba(0, 0, 0, 0.2);
}

ks-navbar-contact-panel .map {
    flex: none;
    background-color: #eeeeee;
}

ks-navbar-contact-panel .info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    flex: 1;
    padding: 20px;

    font-size: 16px;
    text-align: center;
}

ks-navbar-contact-panel .address,
ks-navbar-contact-panel .buttons {
    width: 100%;
}

ks-navbar-contact-panel .buttons a {
    display: inline-block;
    box-sizing: border-box;
    width: 100%;

    margin: 2px;
    padding: 3px;
    font-size: 18px;

    color: #ffffff;
    background-color: transparent;
    border: 1px solid #ffffff;
    border-radius: 500px;

    line-height: 40px;
    text-align: center;
    text-decoration: none;
    text-transform: none;
    vertical-align: middle;

    overflow: visible;
}

@media only screen and (max-width: 639px) {
    ks-navbar-contact-panel .address {
        font-size: 14px;
    }

    ks-navbar-contact-panel .buttons a {
        font-size: 14px;
    }
}

@media only screen and (min-width: 640px) {
    ks-navbar-contact-panel .margin {
        margin-right: 10%;
    }

    ks-navbar-contact-panel .address {
        width: calc(100% * 1 / 3.001);
    }

    ks-navbar-contact-panel .buttons {
        width: calc(100% * 2 / 3.001);
    }
}

@media only screen and (min-width: 1200px) {
    ks-navbar-contact-panel .info > * {
        width: 50%;
    }
}

@media only screen and (max-width: 1200px) {
    ks-navbar-contact-panel .content {
        flex-direction: column-reverse;
        height: calc(100vh - 72px);
    }
}
