import { Component, h, Element, State, Prop } from '@stencil/core';
import { Category } from "../../../../global/data/common";
import { common } from "../../../../global/data/common";
@Component({
    tag: 'ks-category-sidebar',
    styleUrl: 'category-sidebar.css'
})
export class NavbarCategorySidebar {
    @Element() root: HTMLElement;
    @Prop() level: number = 3;
    @State() open: number[] = [];

    click(index: number, level: number) {
        let open = this.open;

        if(open.length == level+1) {
            if(open[level] == index) {
                open.pop()
                console.log(open);
            }

            else open[level] = index;
        }

        else if(open.length > level) {
            const match = open[0] == index;
            open.splice(level, open.length - level);
            
            if(!match)
                open = [...open, index];
        }

        else open = [...open, index];

        this.open = [...open];
        console.log(this.open);
    }

    render() {
        return common.get('categories').map((category, index) => 
            this.element(category, index)
        );
    }

    element(category: Category, index: number, level: number = 0) {
        const hasChildren = 'children' in category && level+1 < this.level;

        const open = this.open.length > 0 && this.open[level] == index;
        const classList = ["category", open ? "open" : null].join(" ");

        return <div class={classList}>
            
            <div class="name" onClick={()=>this.click(index, level)}>
                <span>{category.name}</span>
                {hasChildren ? <ks-icon name={open ? "minus" : "plus"} /> : null}
            </div>
            
            {hasChildren ? 
                <div class="children">
                    {category.children.map((item, index) =>
                        ('children' in item && level < 1) ? 
                            this.element(item, index, level+1) :
                            <a href={item.url}>{item.name}</a>
                    )}

                    <a href={category.url} class="seeall">Zobacz wszystko</a>
                </div>
            : null}
        </div>;
    }
}