import { Component, h, Prop, State } from '@stencil/core';
import { addToCart } from '../../global/functions/addToCart';
import { addToFavourites, removeFromFavourites } from '../../global/functions/favourites';
import { common } from '../../global/data/common';
import { ProductShortData } from '../../global/data/product';
import { ProductCardData } from '../../global/data/listing';
import priceFormat from '../../global/functions/price';

@Component({
  tag: 'ks-product-card',
  styleUrl: 'product-card.css'
})
export class ProductCard {
  @Prop() linkOnly: boolean = false;
  @Prop({reflect: true}) flat: boolean;
  
  @Prop() product: ProductCardData;

  @State() cartLoading: boolean = false;
  @State() favLoading: boolean = false;
  @State() favSuccess: boolean = false;

  @Prop({reflect: true, mutable: true}) squashed: boolean = false;
  
  productShortData: ProductShortData;
  componentWillLoad() {
    this.productShortData = {
      id: this.product.id,
      traitIDs: "",
      sku: this.product.sku,
      name: this.product.name,
      link: this.product.link,
      image: this.product.image,
      imageFull: this.product.imageFull,
      currentPrice: this.product.currentPrice,
      quantity: 1,
      currency: this.product.currency,
      categories: this.product.categories,
      breadcrumbs: this.product.breadcrumbs,
      brandName: this.product.brandName
    };

    if(this.product?.shippingMessage)
      this.squashed = true;
  }

  cart() {
    this.cartLoading = true;

    addToCart(this.productShortData, 1)
      .then(() => this.cartLoading = false);
  }

  favourites() {
    if(this.favLoading) return;
    this.favLoading = true;

    if(this.favSuccess) {
      this.favSuccess = false;
      removeFromFavourites(this.product.id).then(() => {
        this.favLoading = false;
      });
    }
    else {
      addToFavourites(this.productShortData).then(() => {
        this.favSuccess = true;
        setTimeout(() => this.favLoading = false, 200);
      });
    }
  }

  render() {
    const translations = common.get('translations');
    const currentPrice = priceFormat(this.product.currentPrice);
    const previousPrice = priceFormat(this.product.previousPrice);

    return[
        <a href={this.product.link} aria-label={this.product.name} class="top">
            <ks-img2 fill limit center src={this.product.image} webp={this.product.webp} width={280} height={280} alt={this.product.name} />
            <span>{this.product.name}</span>
        </a>,

        <div class="price">
            {this.product.previousPrice ?
                <s class="previous">{previousPrice}</s>
            : null}
            <span class="current">{currentPrice}</span>
        </div>,

        this.product?.shippingMessage ?
          <div class="shipping">
            <ks-icon name="check" size={0.6}/> {this.product.shippingMessage}
          </div>
        : null,

        <div class="bottom">
            { this.product.unavailable ? <a href={this.product.link} class="unavailable">{translations.unavailable}</a>
            : this.linkOnly ?    <a href={this.product.link} class="link">{translations.seeMore}</a>     
            : [
                <button class="fav" onClick={()=>this.favourites()}>
                  { this.favLoading ? <ks-loader /> : <ks-icon name="star" /> }

                  {this.favSuccess ? 
                    <div class="success">
                      <ks-icon name="check" />
                    </div>
                  : null}
                </button>,

                <button class="cart" onClick={()=>this.cart()}>
                  { this.cartLoading ? 
                    <ks-loader large /> :
                    <span>{translations.addToCart}</span>
                  }
                </button>
            ]}
        </div>
    ];
  }
}
