
ks-category-sidebar .category > .name {
    position: relative;
    cursor: pointer;
    padding: 7px 0px 5px 0px;
}

ks-category-sidebar .category > .name > ks-icon {
    position: absolute;
    top: 6px;
    right: 0px;
}

ks-category-sidebar .category > .children {
    overflow: hidden;
    padding: 0px 0px 0px 10px;
    
    font-size: 16px;

    transition: opacity 0.5s ease;
}

ks-category-sidebar .category > .children > a {
    padding: 4px 0px;
}

ks-category-sidebar .category:not(.open) > .children {
    max-height: 0px !important;
    opacity: 0;
}

ks-category-sidebar .category.open > .children {
    padding: 5px 0px 8px 10px;
    opacity: 1;
}

ks-category-sidebar .seeall {
    color: #bbbbbb  !important;
}
